export default {
	data() {
		return {
			content: ""
		}
	},
	methods: {
		submit() {
			// 是否为空
			if (this.content === '') {
				return uni.showToast({
					title: '消息不能为空',
					icon: 'none'
				});
			}
			this.$emit('submit',this.content)
			// 清空输入框
			this.content = ''
		}
	},
}
